* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

form {
  background-color: #613287;
  justify-content: center;
  padding: 12px;
  display: flex;
  box-shadow: 0 3px 6px rgba(0, 0, 0, .16), 0 3px 6px rgba(0, 0, 0, .23);
}

input {
  height: 30px;
  border: 2px solid rgba(0, 0, 0, .8);
  padding: 0 10px;
  font-size: 16px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, .16), 0 3px 6px rgba(0, 0, 0, .23);
}

button {
  height: 35.2px;
  width: 35.2px;
  background-color: #fff;
  border-radius: 5px;
}

svg {
  height: 29px;
  width: 29px;
  border: 2px solid #fff;
  border-radius: 5px;
}

.search-box {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 7px;
  display: flex;
}

button:focus, button:hover {
  border-radius: 5px;
}

.photo-card {
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  box-shadow: 0 2px 5px -1px #e28efe, 0 1px 3px -1px rgba(0, 0, 0, .3);
}

.photo {
  height: 240px;
  width: 400px;
  justify-content: center;
  align-items: center;
  display: flex;
  overflow: hidden;
}

.photo-card img {
  height: 370px;
}

.info {
  flex-direction: row;
  justify-content: center;
  gap: 20px;
  margin: 15px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  gap: 7px;
  font-size: 15px;
  display: flex;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin: 30px;
  display: flex;
}

use {
  fill: #613287;
}

button:hover use, button:focus use {
  fill: #e28efe;
}

